import { mapState, mapGetters, mapActions, mapMutations } from 'vuex'
const lists = [{
		imgSrc: require('../../assets/img/little/1.jpg'),
		_text: '睹物思人',
		time:'2013-08-06'
	},
	{
		imgSrc: require('../../assets/img/little/2.jpg'),
		_text: '家书抵万金',
		time:'2013-08-06'
	}
]
export default {
	data() {
		return {
			someThingsList: lists
		}
	},
	computed: {
		...mapState({
			count: 'list_num'
		})
	},
	methods: {
		fun1(value) {
			console.log(value)
			
			//改变状态
			this.$store.commit('fun1Increment',{
				n:value
			})
			this.$router.push({
				name: 'someThings'
			})
			
		},
		

	}
}



